@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@400;600;700&family=Lexend+Deca:wght@100;200;300;400;500;600;700&family=Poppins:wght@300;400;600&display=swap');

*{
  margin: 0;
  padding: 0;
  background-color: rgb(186, 243, 247);
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

html{
  font-size: 62.5%;
}
h1{
  display: flex;
  justify-content: center;
  margin-top: 10px;
  color: blue;
}
h2{
  display: flex;
  justify-content: center;
  margin-top: 10px;
  color: blue;
}
table {
  margin: 5rem auto;
  border-radius: 1rem;
  border: 1rem hidden #443C68;
  border-collapse: collapse;
  box-shadow: 0 0 0 1px black;
  overflow: hidden;
  max-width: 80vw
}

li, p, a, td{
  font-size: 1.6rem;
  line-height: 1.6;
}

thead {
  font-size: 1.5rem;
  background-color: black;
  color: black;
}

th,
td {
  border: 1px solid black;
  min-width: 8rem;
  padding: 1.5rem;
  line-height: 1.7;
}

td {
  font-size: 1.2rem;
}

